.panorama {
    width: 400px;
    margin-bottom: 20px;
}

.panorama .number {
    font-size: 32px;
}
  
.chartDiv {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-left: 0.1px solid lightgray;
  padding-left: 40px
}
  
.chartDiv > div {
  margin: 0px;
}

.chart {
    display: flex;
    width: 180px;
    margin: 0;
}

.chartDiv div h4 {
    color: var(--primary-blue);
    font-size: large;
    text-align: center;
}

.topChartInfo {
  margin: 15px 0px 15px 0px;
  color: #0f5e72;
}

.topChartInfo p {
  font-size: 15px;
  width: 90px;
}

.bottomChartInfo, .topChartInfo  {
    display: flex;
    line-height: 1;
}

.bottomChartInfo {
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.bottomChartInfo p {
  margin-left: 10px;
}

.bottomChartInfo p:nth-child(2) {
  width: 80px;
}

/* ICONS / ÍCONES */
 
.maisSeries {
    margin-top: 20px;
    text-align: right;
}
  
.maisSeries a {
    color: #0f5e72;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  
.maisSeries a > * {
    background: #0f5e72;
    border-radius: 20px;
    border: 1px solid #0f5e72;
    color: white;
    font-size: 24px;
    margin-left: 5px;
    transition: 0.15s ease;
}
  
.maisSeries:hover a > * {
    transform: translateX(4px);
} 


@media screen and (max-width: 1115px) {
  .panorama {
    width: 100%;
  }
  .chartDiv {
    display: flex;
    justify-content: space-around;
  }
  .chart {
    width: 450px;
  } 
}