.socialmedia {
    display: flex;
    position: fixed;
    top: 40%;
    left: -42px;
    box-shadow: 0px 1px 4px black;
    border-bottom-right-radius: 10px;
    border-top-right-radius: 10px;
    transition: 0.4s;
    z-index: 1;
}

.socialmedia .menu {
    background: #ecd49c;
    display: flex;
    padding: 5px;
    flex-direction: column;
}

.socialmedia .menu a {
    margin: 6px 6px 0 6px;
    font-size: 19px;
    transition: 0.3s ease;
}

.socialmedia a {
    color: #0f5e72;
    text-decoration: none;
}

.socialmedia .menu a:hover {
    transform: scale(1.3) rotate(20deg);
}

.arrowColumn {
    background: #e6c988;
    cursor: pointer;
    border-bottom-right-radius: 10px;
    border-top-right-radius: 10px;
    min-width: 14px;
    padding: 1px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.1s ease-out
}

.arrowColumn > * {
    transform: rotate(-90deg) translateY(-5px);
    color:#0f5e72;
    font-size: 18px;
    transition: 0.1s ease-out;
}

.arrowColumn:hover > * {
    font-size: 22px;
}

@media screen and (max-width: 1115px) {
    .socialmedia {
        display: none;
    }
} 