.mainCarousel {
    background: #086688;
    color: #ffffff;
    display: flex;
    height: 240px;
    position: relative;
    top: 120px;
}

.mainText {
    /* height: 40%; */
}

.title {
    height: 20%;
    color: #e0d783;
    font-size: 24px;
    font-family: Cambria, Cochin, Georgia, Times, serif;
    transition: 0.6s;
}

.fakeImage {
    width: 100%;
    height: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-family: serif;
    font-size: 48px;
    transition: 0.6s;
}

.arrowColumn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.625rem;
    cursor: pointer;
    color: #fff;
}

.arrowColumn:hover {
    opacity: 0.4;
}

.column {
    /* flex: 1; */
    width: 80%;
}

#firstColumn {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 15px;
}

.bottomBtns {
    display: flex;
    /* align-items: center; */
    /* margin-top: 40px; */
}

.carouselBtn {
    flex: 1;
}

.carouselBtn button {
    height: 3px;
    width: 40px;
    border: none;
    margin: 0.125rem;
    cursor: pointer;
    transition: 0.5s;
}

.carouselBtn button:hover {
    background: gray;
}

.doubleBtn {
    display: flex;
    align-items: center;
}

.doubleBtn button {
    background: transparent;
    color: #fff;
    border: none;
    box-shadow: 0 0 0px 0.8px white;
    cursor: pointer;
}

.doubleBtn button:nth-child(1) {
    font-size: 24px;
    border-radius: 2px 0 0 2px;
}

.doubleBtn button:nth-child(2) {
    border-radius: 0 2px 2px 0;
    padding: 0.525rem; 
}


@media screen and (max-width: 1115px) {
    .mainCarousel {
        top: 148px;
        height: 190px;
    }
    .mainText {
        display: none;
    }
}

@media screen and (max-width: 1007px) {
    .mainCarousel {
        height: 160px;
        top: 148px;
    }
    .mainText {
        display: none;
    }
    
    .title {
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

@media screen and (max-width: 750px) {  
    .mainCarousel {
        height: 240px;
        display: flex;
        flex-direction: column;
        flex-flow: column-reverse;
    }
    .fakeImage {
        height: 200px;
    }
} 