.bottomcomponent {
    display: flex;
    flex-wrap: wrap;
    border-left: 1.6px solid lightgray;
    width: 400px;
}

.linkBox:hover {
    box-shadow: 0.8px 0.6px 5px rgb(65, 65, 65);
    transition: 0.2s ease
}

.linkBox {
    display: flex;
    align-items: center;
    border-radius: 5px;
    width: 160px;
    margin: 5px 15px;
    height: 60px;
    background-color: rgb(238, 238, 238);
    text-decoration: none;
    color: #0f5e72;
}

.linkBox .text {
    margin-left: 5%;
}

.linkBox .icon {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px 0 0 5px;
    background: #0f5e72;
    height: 100%;
    width: 30%;
    color: white;
    font-size: 20px;
}

@media screen and (max-width: 1115px) {
    .bottomcomponent {
        display: flex;
        flex-direction: row;
      width: 100%;
    }
    .linkBox {
        width: 45%;
    }
    .linkBox .icon {
        width: 10%;
    }
  }