.cotation {
    width: 380px;
    margin-right: 50px;
    border-left: 1.6px solid lightgray;
    padding-left: 15px;
}

div > h2 {
    margin-bottom: 10px;
}

.currencyList {
    display: flex;
}

.currencyList div {
    flex: 1;
}


.cotation .column {
    margin-bottom: 10px;
}

.column p {
    margin-bottom: 5px;
}

.column p:nth-child(2), .column p:nth-child(3) {
    font-size: 14px;
    padding-bottom: 5px;
    border-bottom: 1px dotted lightgray;
}

.column:nth-child(2), .column:nth-child(3) {
    text-align: right;
}

.boldText {
    font-weight: 600;
    color: #156a92;
}