.services {
    display: flex;
    flex-direction: column;
    width: 400px;
    padding: 20px;
}

.services h2 {
    margin-bottom: 10px;
}

.servicesBox {
    padding-left: 30px;
    border-left: 1.6px solid lightgray;
}

.servicesBox li {
    list-style: none;
}

.upperList {
    display: flex;
    margin-bottom: -23px;
    margin-left: 5px;
}

.upperList .upperBtn::after {
    content: "";
    position: absolute;
    top: -1px;
    left: -1px;
    border-top: 18px solid #fff;
    border-right: 18px solid transparent;
}

.upperList .upperBtn {
    position: relative;
    z-index: 3;
    display: flex;
    justify-items: center;
    align-items: center;
    text-align: center;
    height: 45px;
    padding: 5px 15px 5px 15px;
    margin-right: 2.5px;
    border-top-right-radius: 8px;
    border-top: 1px solid white;
    border-right: 1px solid white;
    border-left: 1px solid white;
    cursor: pointer;
    font-size: 12px;
}

.upperList .upperBtn p {
    width: 80px;
    margin-left: 5px;
}

.activeUpBtn {
    text-align: center;
}

.tabLine {
    position: relative;
    z-index: 2;
    top: -4.2px;
}

.menuList::after {
    content: "";
    position: absolute;
    bottom: -1px;
    right: -1px;
    border-bottom: 60px solid #fff;
    border-left: 60px solid transparent;
}

.menuList {
    position: relative;
    z-index: 1;
    background: #ebebeb;
    display: flex;
    padding: 10px;
    transition: 0.6s;
    flex-wrap: wrap;
    min-height: 300px;
    min-width: 330px;
    border: 1px solid rgb(226, 226, 226);
}

.menuList li {
    display: flex;
    align-items: center;
    cursor: pointer;
    margin: 0 15px 10px 0;
}

.menuList li img {
    margin-right: 5px;
    transition: 0.2s ease;
}

.menuList li:hover img {
    transform: translateX(4px);
}

.menuList li:hover a {
    text-decoration: underline;
}

.menuList a {
    color: var(--primary-blue);
    text-decoration: none; 
}

.div:nth-child(1) {
    text-align: right;
}

.line {
    display: block;
    width: 2.2px;
    height: 24.2px;
    border-right: 1px solid transparent;
    transform: rotate(45deg);
    position: absolute;
    left: 7px;
    bottom: 24.2px;
}


/* ICONES / ICONS */
.maisServiços {
    text-align: right;
}

.maisServiços a {
    color: #0f5e72;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.maisServiços a > * {
    background: #0f5e72;
    border-radius: 20px;
    border: 1px solid #0f5e72;
    color: white;
    font-size: 24px;
    margin-left: 5px;
    transition: 0.15s ease;
}

.maisServiços:hover a > * {
    transform: translateX(4px);
}

