.notasimprensa {
    width: 400px;
    padding: 20px;
}

.notasimprensa h2 {
    font-weight: 400;
    margin-bottom: 20px;
}

.notasimprensa a {
    text-decoration: none;
}

.nota a {
    color: #10a5ca;
}

.nota span {
    margin-right: 4px;
}

.notas {
    border-left: 1.6px solid lightgray;
    padding-left: 15px;
}

.nota {
    margin: 12px 0 0 0;
}

.nota span {
    color: #0f5e72;
}

span > * {
    transition: 0.2s;
}

.nota:hover span * {
    transform: translateX(-4px);
}

.maisNotas {
    margin-top: 10px;
}

.maisNotas a {
    color: #0f5e72;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.maisNotas a > * {
    background: #0f5e72;
    border-radius: 20px;
    border: 0.4px solid #0f5e72;
    color: white;
    font-size: 24px;
    margin-left: 5px;
    transition: 0.15s ease;
}

.maisNotas:hover a > * {
    transform: translateX(4px);
}
