.navbar {
    position: fixed;
    z-index: 5;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.navbar .top {
    height: 55px;
    background: white;
}

.actualNavbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 65px;
    padding: 0 10px 0 10px;
    background: rgb(248, 248, 248);
    border-top: 1px solid rgb(216, 216, 216);
    border-bottom: 0.2px solid var(--primary-yellow);
}

.column {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.column a {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    margin-right: 40px;
    width: 90px;
    height: 100%;
    text-decoration: none;
    color: var(--primary-blue);
}


.column:nth-child(4) {
    width: 32%;
}

.column a:hover {
    background: white;
}

.column a p {
    font-size: 14px;
    font-weight: 600;
    text-decoration: none;
}

.column:nth-child(2) {
    text-align: right;
}

.search {
    font-size: 27px;
    width: 10px;
}

.logoShape {
    transform: translateY(-14px);
}

.barsMenu {
    display: none;
    margin: 5px 0 5px 0;
    border: none;
    border-radius: 4px;
    background: transparent;
    cursor: pointer;
    flex-direction: column;
}

.menuLine {
    background: var(--primary-blue);
    width: 35px;
    height: 2px;
    border-radius: 5px;
    margin: 8px 0 8px 0;
    transition: 0.1s ease-in;
}

.logo {
    position: absolute;
    z-index: 1;
    transform: translate(90px, -40px);
}

@media screen and (max-width: 1115px) {
    .column:nth-child(2) *, .column:nth-child(4) * {
        display: none;
    }
    .barsMenu {
        display: block;
        z-index: 1;
    }
}