.footer::after {
    content: "";
    position: absolute;
    top: -1px;
    right: -1px;
    border-top: 45px solid #fff;
    border-left: 45px solid transparent;
}

.footer {
    position: relative;
    z-index: 1;
    display: flex;
    font-size: 14px;
    background: rgb(248, 248, 248);
    border-top: 0.2px solid lightgray;
    border-right: 0.2px solid lightgray;
    padding:  20px 30px;
    transform: translateX(-1px);
    width: 99.9%;
    height: 80px;
}

.footer a {
    color: var(--primary-blue);
    text-decoration: none;
}

.footer a:hover {
    text-decoration: underline
}

.boldText {
    color: #0f5e72;
    font-style: italic;
    font-size: 15px;
}

.footer div:nth-child(2) {
    text-align: right;
}

.upArrow {
    cursor: pointer;
    display: flex;
    align-items: center;
    z-index: 2;
    font-size: 24px;
    margin: 0 15px 0 15px;
    transition: 0.2s ease;
    background: #0f5e72;;
    padding: 2px 4px;
    border-radius: 6px;
    height: 34px;
    width: 34px;
    transform: translateX(20px);
}

.upArrow:hover {
    transform: translate(20px, -10px)
}

.upArrow > * {
    color: white;
}

.line {
    display: block;
    z-index: 1;
    width: 4px;
    height: 60px;
    border-right: 1px solid lightgray;
    transform: rotate(315deg);
    position: absolute;
    left: 98.2%;
    bottom: 27px;
}

@media screen and (max-width: 968px) {
    .footer {
        display: flex;
    }
    .line {
        left: 60%;
    }
}