@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,300;1,300&display=swap');

.conversor {
  border-left: 0.8px solid lightgray;
  padding-left: 15px;
}

h2 {
    font-weight: 500;
    color: var(--primary-blue)
}

.conversor input {
    height: 28px;
    text-align: right;
    width: 7rem;
    padding: 5px 6px 6px 5px;
    font-family: 'Cormorant Garamond', serif;
    font-size: 23px;
    color: rgb(70, 70, 70);
    border: 1px solid #137a97;
    border-radius: 2.8px;
}

.smallText {
    border-bottom: 1px solid lightgray;
}

.row, .rowFirst {
    display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 380px;
}

.rowFirst {
    width: 412px;
}

.select {
    margin: 8px 0 5px 0;
}

.select .menu {
    display: flex;
    align-items: center;
    flex-direction: row;
    background: white;
    height: 32px;
    border-radius: 5px;
    border: none;
    padding: 10px 0px 10px 0px;
    border: 3.0px solid white;
}

.select .menu .arrow {
  width: 20px;
}
  
.select .menu img {
    margin-right: 5px;
}
  
.select button:focus {
    border: 3.0px solid lightblue;
}

.dropdown {
    position: absolute;
    background: white;
    border: 1.2px solid gray;
    z-index: 2;
    flex-direction: column;
    transform: translate(0, 0px);
    padding: 5px 0px 5px 0px;
}
  
.dropdownItems {
    display: flex;
    align-items: center;
    padding: 5px 12px 5px 12px
}

.dropdownItems img {
    margin-right: 10px;
}
  
.dropdownItems:hover {
    background: rgb(228, 228, 228);
}
  
.output::before {
    content: '';
    position: absolute;
    bottom: -5px;
    right: -5px;
    border-bottom: 22px solid #fff;
    border-left: 22px solid transparent;
}

.output {
  position: relative;
  z-index: 1;
}
  
.line {
    display: block;
    width: 1px;
    height: 16px;
    border-left: 1px solid transparent;
    transform: rotate(45deg);
    position: absolute;
    right: 4.8px;
    bottom: -2px;
    border-color: #137a97;
}

.redo {
    margin-left: 15px;
    font-size: 18px;
    cursor: pointer;
    color: #137a97;
    transition: 1s;
}

.redo:hover {
    transform: rotate(360deg);
}

.bottomInfo {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.bottom {
    font-size: 11px;
    text-align: left;
    width: 100px;
}

.mais {
    margin-top: 20px;
    text-align: right;
  }
  
.mais a {
    color: #0f5e72;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  
.mais a > * {
    background: #0f5e72;
    border-radius: 20px;
    border: 1px solid #0f5e72;
    color: white;
    font-size: 24px;
    margin-left: 5px;
    transition: 0.15s ease;
}
  
.mais:hover a > * {
    transform: translateX(4px);
} 

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}