.mainComponent {
    padding: 20px;
    display: flex;
    flex-direction: row;
    position: relative;
    margin-top: 110px;
    margin-bottom: 25px;
    align-items: baseline;
    justify-content: space-around;
}

.sMainFirstRow {
    display: flex;
    flex-direction: row;
}

/* .sMainFirstRow div {
    background: red;
} */

@media screen and (max-width: 1115px) {
    .mainComponent {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 150px;
    }
}